import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';

const WatchedFilm = ({ film, deleteFromWatched }) => {
  return (
    <div>
      <h3> {film.title} </h3>

      <button type="button" onClick={() => deleteFromWatched(film.id)}>
        Delete
      </button>

      <button type="button">
        <NavLink to={`/${film.title}/${film.id}`}> Show more </NavLink>
      </button>
    </div>
  );
};
WatchedFilm.propTypes = {
  film: PropTypes.shape({
    id: PropTypes.number.isRequired,
    title: PropTypes.string.isRequired,
  }).isRequired,
  deleteFromWatched: PropTypes.func.isRequired,
};
export default WatchedFilm;
